@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
	box-sizing: border-box;
}

html {
	font-family: "Noto Sans JP", sans-serif;
}

body {
	margin: 0;
	background-color: wheat;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.expenses-filter {
	color: black;
	padding: 0 1rem;
}

.expenses-filter__control {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: space-between;
	margin: 1rem 0;
}

.expenses-filter label {
	font-weight: bold;
	margin-bottom: 0.5rem;
}

.expenses-filter select {
	font: inherit;
	padding: 0.5rem 3rem;
	font-weight: bold;
	border-radius: 6px;
}

.expenses {
	padding: 1rem;
	background-color: rgb(10, 10, 10);
	margin: 2rem auto;
	width: 50rem;
	max-width: 95%;
}

.Card {
	border-radius: 12px;
	box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
	background-color: #0cecdd;
}

.new-expense {
	background-color: #fff338;
	padding: 1rem;
	margin: 2rem auto;
	width: 50rem;
	max-width: 95%;
	border-radius: 12px;
	text-align: center;
	box-shadow: 0 1px 8px #7c83fd;
	-webkit-text-emphasis-color: #7c83fd;
	        text-emphasis-color: #7c83fd;
}

.new-expense button {
	font: inherit;
	cursor: pointer;
	padding: 1rem 2rem;
	border: 1px solid #c400ff;
	background-color: #9a09dd;
	color: white;
	border-radius: 12px;
	margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
	background-color: #be4de0;
	border-color: #510674;
}

.new-expense button.alternative {
	color: #220131;
	border-color: transparent;
	background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
	background-color: #ddb3f8;
}

.new-expense__controls {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 1rem;
	gap: 1rem;
	margin-bottom: 1rem;
	text-align: left;
}

.new-expense__control label {
	font-weight: bold;
	margin-bottom: 0.5rem;
	display: block;
}

.new-expense__control input {
	font: inherit;
	padding: 0.5rem;
	border-radius: 6px;
	border: 1px solid #ccc;
	width: 20rem;
	max-width: 100%;
}

.new-expense__actions {
	text-align: right;
}

.expense-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	box-shadow: 0 2px 8px rgba(219, 9, 9, 0.25);
	background-color: #d432bc;
	padding: 0.5rem;
	margin: 1rem 0;
}

.expense-item__description {
	display: flex;
	flex-direction: column;
	color: #000;
	grid-gap: 1rem;
	gap: 1rem;
	align-items: flex-end;
	flex-flow: column-reverse;
	justify-content: flex-start;
	flex: 1 1;
}

.expense-item h2 {
	color: #c400ff;
	font-size: 1rem;
	flex: 1 1;
	margin: 0 1rem;
	color: white;
}

.expense-item__price {
	font-size: 1rem;
	font-weight: bold;
	color: white;
	background-color: #9a09dd;
	border: 1px solid white;
	padding: 0.5rem;
	border-radius: 12px;
}

@media (min-width: 580px) {
	.expense-item__description {
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		flex: 1 1;
	}

	.expense-item__description h2 {
		font-size: 1.25rem;
	}

	.expense-item__price {
		font-size: 1.25rem;
		padding: 0.5rem 1.5rem;
	}
}

.expense-date {
	display: flex;
	flex-direction: column;
	width: 5.5rem;
	height: 5.5rem;
	border: 1px solid #ececec;
	background-color: #c3b4f3;
	color: black;
	border-radius: 12px;
	align-items: center;
	justify-content: center;
}

.expense-date__month {
	font-size: 0.75rem;
	font-weight: bold;
}

.expense-date__year {
	font-size: 0.75rem;
}

.expense-date__day {
	font-size: 1.5rem;
	font-weight: bold;
}

.expenses-list {
	list-style: none;
	padding: 0;
}

.expenses-list__fallback {
	color: black;
	text-align: center;
}

.chart-bar {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.chart-bar__inner {
	height: 100%;
	width: 100%;
	border: 1px solid #313131;
	border-radius: 12px;
	background-color: #c3b4f3;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}

.chart-bar__fill {
	background-color: #c400ff;
	width: 100%;
	transition: all 0.3s ease-out;
}

.chart-bar__label {
	font-weight: bold;
	font-size: 0.5rem;
	text-align: center;
}

.chart {
	padding: 1rem;
	border-radius: 12px;
	background-color: #fff338;
	text-align: center;
	display: flex;
	justify-content: space-around;
	height: 10rem;
}

h1 {
	color: black;
	font-family: "Lato", sans-serif;
	font-size: 54px;
	font-weight: 300;
	line-height: 58px;
	text-align: center;
	margin: 0 0 58px;
}

